import React, { Component } from "react";
import {
  Form,
  Grid,
  Header,
  Segment,
  TransitionablePortal,
  Button
} from "semantic-ui-react";

const Quotes = [
  {
    header: "Thomas Hauser",
    quote:
      'Not voting, is disrespecting the "best" of what our nation stands for.'
  },
  {
    header: "Unknown",
    quote: "The right of election is the very essence of the constitution."
  },
  {
    header: "Patty Berg",
    quote: "People are what any election is all about."
  }
];

export default class TransitionablePortalExampleTransition extends Component {
  state = { animation: "fade up", duration: 1200, open: false };

  handleClick = () => this.setState({ open: !this.state.open });

  renderPortalContent = () => {
    return (
      <Segment
        style={{
          left: "40%",
          position: "fixed",
          top: "50%",
          zIndex: 1000
        }}
      >
        <Header>This is a controlled portal</Header>
        <p>Portals have tons of great callback functions to hook into.</p>
        <p>To close, simply click the close button or click away</p>
      </Segment>
    );
  };

  render() {
    const { animation, duration, open } = this.state;

    return (
      <Grid columns={2}>
        <Grid.Column>
          <Form>
            <Form.Button
              content={open ? "Close Portal" : "Open Portal"}
              negative={open}
              positive={!open}
              onClick={this.handleClick}
            />
          </Form>
          <Button.Group size="small">
            <Button>One</Button>
            <Button>Two</Button>
            <Button>Three</Button>
          </Button.Group>
          <TransitionablePortal
            open={open}
            transition={{ animation, duration }}
          >
            {this.renderPortalContent()}
          </TransitionablePortal>
        </Grid.Column>
      </Grid>
    );
  }
}
